import Vue from 'vue'
import VueX from 'vuex'
import actions from './action.js';

Vue.use(VueX)

//
const state = {
  baseData: {},
}

//  mutation 
const mutations = {
  // mutation  state
  //  state
  SETDATA (state, data) {
  	state.baseData = data;
  },
}

const getters = {
	baseData: state => state.baseData,
}

//  store
//  store 
export default new VueX.Store({
  state,
  mutations,
  actions,
  getters
})



