/**
 * 
 *
 * cookie localStorage
 *
 * author: renzhenguo
 */
let data = {}

//cookie
data.addCookie = function(objName, objValue, objHours){
  var str = objName + "=" + encodeURIComponent(objValue);
  if (objHours > 0) {
    //cookie
    var date = new Date();

    var ms = objHours * 3600 * 1000;

    date.setTime(date.getTime() + ms);

    str += "; expires=" + date.toGMTString()+";path=/";
  }
  document.cookie = str;
}

export default data;



